.navigation {
    display: flex;
    padding: 22px 0 32px;

    &__li {
        padding-right: 74px;
        position: relative;
    }

    &__li:hover &__drop-menu {
        display: block;
        z-index: 1;
        transition-duration: 1s;
    }

    &__li:hover &__link {
        color: $purple;
        transition-duration: 1s;
    }

    &__link {
        color: $grey1;
        font-weight: 400;
        font-size: 18px;
        line-height: 25px;
        position: relative;
        padding-bottom: 20px;
    }

    &__link::after {
        content: "";
        background-image: url("../img/nav-arrow.svg");
        width: 13px;
        height: 7px;
        position: absolute;
        right: -20px;
        top: 7px;
    }

    &__drop-menu {
        display: none;
        position: absolute;
        width: 168px;
        background: $white;
        left: 0;
        top: 30px;
        font-size: 16px;
        line-height: 19px;
        padding: 16px 16px 8px;
        border: 0.25px solid $grey5;
        box-shadow: 0px 14px 32px rgba(0, 0, 0, 0.11),
            inset 0px 0px 1px #c5c5c5;
        border-radius: 8px;
    }

    &__drop-menu-li {
        padding-bottom: 8px;
    }

    &__li:hover &__link::after {
        transform: rotate(180deg);
        transition-duration: 0.5s;
    }

    &__drop-menu-link {
        color: $grey2;
    }

    &__drop-menu-link:hover {
        text-decoration-line: underline;
        color: $grey1;
        transition-duration: 1s;
    }

    &__mini {
        display: none;
        font-weight: bold;
        font-size: 18px;
        line-height: 110%;
        color: $grey2;
        border: none;
        background-color: #FBFBFB;
        padding: 14px 41px 14px 16px;
        text-align: start;   
        position: relative;   
    }

    &__mini::after{
        content: '';
        background-image:url(../img/right-arrow.svg);
        background-repeat: no-repeat;
        background-position: center center;
        width: 24px;
        height: 24px;
        position: absolute;
        right: 38px;
        top: 50%;
        transform: translateY(-50%);
    }

}

@media (max-width: 375px){
    .navigation {
        display: none;

        &__mini {
            display: block;
            
            width: 109%;
        }
    }


}